import React, { useCallback, useState, useEffect } from 'react';
import {
    Select,
    Layout,
    FormLayout,
    TextField,
    Banner,
    Card,
    Stack,
    Button,
    Collapsible,
    TextContainer,
    Checkbox,
    Icon,
    Autocomplete,
    Scrollable,
    Tag,
    SelectOption,
    ContextualSaveBar,
    Link
} from '@shopify/polaris';
import Listings from "../Listings"

import { XYPlot, XAxis, YAxis, HorizontalGridLines, LineSeries, RadialChart } from 'react-vis';

const Form: React.FC = () => {
    const myData = [{ angle: 30, label: "asdasd", color: "3" }, { angle: 5 }]
    return <div>
        <Layout>
            <Layout.Section secondary>
                <span className="stats">
                    <Card title="Overall shop score" sectioned>
                        <Banner onDismiss={() => { }} status="warning">
                            <p>
                                You have SEO issues on home page {' '}
                                <Link url="">Go to fix</Link>
                            </p>
                        </Banner>
                        <h1 style={{
                            fontSize: "50px",
                            textAlign: "center",
                            margin: "44px"
                        }}>7/10</h1>
                    </Card>
                </span>

            </Layout.Section>
            <Layout.Section secondary>
                <span className="stats">
                    <Card title="Products score" sectioned>
                        <RadialChart
                            data={myData}
                            width={300}
                            height={300} />
                    </Card>
                </span>

            </Layout.Section>
            <Layout.Section secondary>
                <span className=" stats">
                    <Card title="Collections score" sectioned>
                    </Card>
                </span>
            </Layout.Section>
            <Layout.Section secondary>
                <span className="stats">
                    <Card title="Some other stats" sectioned>
                    </Card>
                </span>
            </Layout.Section>
            <span style={{ marginRight: "20px", visibility: "hidden" }}>
                <Layout.Section secondary>
                    <Card sectioned>
                    </Card>
                </Layout.Section>
            </span>

        </Layout >
        <Listings></Listings>
    </div>
}

export default Form