import React, { Component, useState, useEffect } from 'react';
import {
  GridComponent,
  ColumnDirective,
  ColumnsDirective,
  Page,
  Inject,
  Group,
  Sort,
  Edit,
  EditSettingsModel,
  Toolbar,
  ToolbarItems,
  CommandColumn
} from '@syncfusion/ej2-react-grids';
import { Button } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import * as service from "../../services/productTypes"
import { useHistory, useRouteMatch } from "react-router-dom";
import ProductTypesToolBar from "./ProductTypesToolBar"
const useStyles = makeStyles({
  productTitle: {
    alignItems: 'center'
  },
  title: {
    fontSize: 24,
  }
});

const Listing: React.FC = () => {
  const [productTypes, setProductTypes] = useState([]);
  const [error, setError] = useState();
  const [page, setPage] = useState(1);
  const history = useHistory();
  let grid: any = null;
  let match = useRouteMatch();
  useEffect(() => {
    getAllProducttypes();
  }, [page]);

  const classes = useStyles();
  const editOptions: EditSettingsModel = { allowEditing: true, allowAdding: true, allowDeleting: true };
  const toolbarOptions: ToolbarItems[] = ['Add', 'Edit', 'Delete'];

  const getAllProducttypes = () => {
    return service.getAll().then(data => setProductTypes(data)).catch(err => setError(err.toString()));
  }

  function rowSelected() {
    const selectedRow: any = grid && grid.getSelectedRecords();
    const productType: any = selectedRow && selectedRow[0];
    const id: any = productType && productType._id;
    history.push(`${match.path}/${id}`)
  }

  const pageSettings = {
    pageSize: 6
  }
  function products(){
    return <div>{Math.floor(Math.random() * 20) }</div>
  }
  return (
    <Card variant="outlined">
      <CardContent>
        <ProductTypesToolBar />
        <GridComponent ref={g => grid = g} rowSelected={rowSelected} dataSource={productTypes} enableHover={true} rowHeight={38} allowPaging={true} pageSettings={pageSettings} allowFiltering={true} allowSorting={true} allowSelection={true}>
          <ColumnsDirective>
            <ColumnDirective field='name' headerText='Name' width='180' />
            <ColumnDirective field='products' headerText='Products' width='180' template={products} />
          </ColumnsDirective>
          <Inject services={[Page, Group, Sort, Edit, Toolbar, CommandColumn]} />
        </GridComponent>
      </CardContent>
    </Card>
  );
}

export default Listing;