import React, { useCallback, useState, useEffect } from 'react';
import {
    Select,
    Layout,
    FormLayout,
    TextField,
    Banner,
    Card,
    Stack,
    Button,
    Collapsible,
    TextContainer,
    Checkbox,
    Icon,
    Autocomplete,
    Scrollable,
    Tag,
    SelectOption,
    ContextualSaveBar
} from '@shopify/polaris';
import { Vendor } from '../../Models/Vendor';
import { ProductType } from '../../Models/ProductType';
import { TagModel } from '../../Models/Tag';
import * as vendorService from "../../services/productVendors"
import * as productTypeService from "../../services/productTypes"
import * as productService from "../../services/products"
import * as tagsService from "../../services/tags"

import { toArrayOfProps, Column } from "../../utils/dataTableHelper"
import { useSelectData } from "../../Hooks/useSelectData"
import { useTags as useAutocomplete } from "../../Hooks/useAutocomplete"
import Product from '../../Models/ProductDetails';

const Form: React.FC = () => {

    const [vendorOptions, vendor, handleVendorSelectChange] = useSelectData<Vendor>(vendorService.getAll, "name")
    const [productTypeOptions, product_type, handleProductTypeChange] = useSelectData<ProductType>(productTypeService.getAll, "name")
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [tagOptions, tagsMarkup, selectedTagOptions, handleTagSelectChange, tagTextField] = useAutocomplete<TagModel>(tagsService.getAll, "name", "Tags")
    const [collectionOptions, collectionsMarkup, selectedcollectionOptions, handleCollectionSelectChange, collectionTextField] = useAutocomplete<TagModel>(tagsService.getAll, "name", "Collections")

    const [templateselected, settemplateSelected] = useState('product.pre-order');

    const handleTemplateSelectChange = useCallback((value) => settemplateSelected(value), []);

    const [isDirty, setIsDirty] = useState(true);

    const templateoptions = [
        { label: 'product.pre-order', value: 'product.pre-order' },
        { label: 'product', value: 'product' },
        { label: 'product.coming-soon', value: 'product.coming-soon' },
    ];
    const toggleIsDirty = useCallback(
        () => setIsDirty((isDirty) => !isDirty),
        [],
    );

    const saveChanges = () => {
        const product: Product = {
            title,
            description,
            product_type,
            vendor,
        }
        productService.create(product).then(data => {
            toggleIsDirty()
        })
    }

    const contextualSaveBarMarkup = isDirty ? (
        <ContextualSaveBar
            message="Unsaved product"
            saveAction={{
                onAction: saveChanges,
            }}
            discardAction={{
                onAction: toggleIsDirty,
            }}
        />
    ) : null;
    return <Layout>
        {contextualSaveBarMarkup}
        <Layout.Section>
            <Card sectioned>
                <FormLayout>
                    <FormLayout.Group>
                        <Select
                            label="Vendor"
                            options={vendorOptions}
                            onChange={(data) => {
                                setTitle(`${data} ${product_type}`)
                                handleVendorSelectChange(data)
                            }}
                            placeholder="Please select product vendor"
                            value={vendor}
                        />
                        <Select
                            label="Type"
                            options={productTypeOptions}
                            placeholder="Please select product type"
                            onChange={(data) => {
                                setTitle(`${vendor} ${data}`)
                                handleProductTypeChange(data)
                            }}
                            value={product_type}
                        />
                    </FormLayout.Group>
                    <FormLayout.Group>
                        <TextField
                            type="text"
                            label="Title"
                            value={title}
                            onChange={(value: string) => setTitle(value)}
                        />
                    </FormLayout.Group>
                    <FormLayout.Group>
                        <TextField
                            label="Description"
                            value={description}
                            onChange={(value: string) => setDescription(value)}
                            multiline={4}
                        />
                    </FormLayout.Group>
                </FormLayout>
            </Card>
        </Layout.Section>
        <Layout.Section secondary>
            <Card title="Product availability" sectioned actions={[{ content: 'Manage', onAction: () => { } }]} >
                <TextContainer>Online store</TextContainer>
                <Stack vertical>
                    <Collapsible
                        open={false}
                        id="basic-collapsible"
                        transition={{ duration: '150ms', timingFunction: 'ease' }}
                    >
                        <FormLayout>
                            <Checkbox
                                label="Point of sale"
                                checked={false}
                            // onChange={handleChange}
                            />
                            <Checkbox
                                label="Some other channel"
                                checked={false}
                            // onChange={handleChange}
                            />
                        </FormLayout>
                    </Collapsible>
                </Stack>
            </Card>
            <Card title="Organization" sectioned>
                <div className="prod-grouping">
                    <Autocomplete
                        allowMultiple
                        options={collectionOptions}
                        selected={selectedcollectionOptions}
                        textField={collectionTextField}
                        onSelect={handleCollectionSelectChange}
                        listTitle="Collection"
                    />
                    <br />
                    <Autocomplete
                        allowMultiple
                        options={tagOptions}
                        selected={selectedTagOptions}
                        emptyState={<div>add?</div>}
                        textField={tagTextField}
                        onSelect={handleTagSelectChange}
                        listTitle="Suggested Tags"
                    />
                    <br />
                    {tagsMarkup}
                </div>

            </Card>
            <Card title="Theme templates" sectioned>
                <Select
                    label="Template suffix"
                    options={templateoptions}
                    onChange={handleTemplateSelectChange}
                    value={templateselected}
                />
            </Card>
        </Layout.Section>

    </Layout>
}

export default Form