import React, { useState } from "react";
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
        },
    },
}));

export default function Options(props) {
    // const [selectedOptionID, setProductOption] = useState("");
    const optionChange = (event) => {
        const optionID = event.target.options[event.target.selectedIndex].value;
        props.onchange(props.index, optionID);
    };

    // const [selectedOptionID_2, setProductOption_2] = useState("");
    // const optionChange_2 = (event) => {
    //     const optionID_2 = event.target.options[event.target.selectedIndex].value;
    //     setProductOption_2(optionID_2);
    // };

    const classes = useStyles();
    return <div>
        {/* {options.map(option => <OptionItem key={option.ID} Id={option.ID} />)} */}
            <FormControl variant="outlined" className={classes.formControl}>
                <InputLabel htmlFor="outlined-age-native-simple">Choose option</InputLabel>
                    <Select
                            onChange={optionChange}
                            native
                            disabled={props.disabled}
                            inputProps={{
                                name: 'chooseOption',
                                id: 'outlined-age-native-simple',
                            }}
                        >
                            <option value="" />
                            <option value={1}>Color</option>
                            <option value={2}>Size</option>
                            <option value={3}>Price</option>
                            <option value={4}>Quantity</option>
                        </Select>
                    </FormControl>          
           
        </div>
}