import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import SaveIcon from '@material-ui/icons/Save';
import Button from '@material-ui/core/Button';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Grid from '@material-ui/core/Grid';
import Variants from "./Options";
import data from '../data/variants.json';

    
const useStyles = makeStyles(theme => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));


export default function Inputs() {
  const classes = useStyles();
  const [values, setValues] = useState({
    amount: '',
  });

  var enableTitle = true;


  const [productType,setProductType] = useState("");
  const [productTitle, setProductTitle] = useState("");
  const [productVendor, setProductVendor] = useState("");
  const [optionsArray, setOptionsArray] = useState([ '', '', '' ]);
  const [generatedOptionsArray, setGeneratedOptionsArray] = useState([]);

  function optionsChange(index, value) {
    optionsArray[index] = value;
    setOptionsArray(Object.assign([], optionsArray));
  
    setGeneratedOptionsArray(generateOptions());
  }
  
  function generateOptions(name = '', start = 0, generatedOptions = []) {
    let i = start;

    data.map(item => {
      if(item.optionID == optionsArray[i]) {
        if(optionsArray[i+1] != '' && i+1 < optionsArray.length) {
          generateOptions(name + ' ' + item.value, i+1, generatedOptions);
        }
        else generatedOptions.push(name + ' ' + item.value);
      } 
    });

    return generatedOptions;
  }

  const handleChange = prop => event => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleTitleChange = (event) => {
    setProductTitle(event.target.value);
  };

  const productTypeChange = (event) => {
    const productTypeOption = event.target.options[event.target.selectedIndex].text;
    setProductType(productTypeOption);
    setProductTitle(productTypeOption + " " + productVendor)
  };

  const productVendorChange = (event) => {
    const productVendorOption = event.target.options[event.target.selectedIndex].text;
    setProductVendor(productVendorOption);
    setProductTitle(productType + " " + productVendorOption);
  };

  if (productType != "" && productVendor != "") {
    enableTitle = false;
  }



  return <div className="container">
  <h2 style={{ textAlign: 'center', paddingTop: '15px' }}>EDIT PRODUCT</h2>
  <form className={`${classes.root} form`} noValidate autoComplete="off"> 
    <Grid className="form-container" container spacing={3}> 

      <Grid item xs={6} className="productType-container">
          <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel htmlFor="outlined-age-native-simple">Product type</InputLabel>
            <Select
              onChange={productTypeChange}
              native
              inputProps={{
                name: 'productType',
                id: 'outlined-age-native-simple',
              }}
            >
              <option value="" />
              <option value={1}>some products</option>
              <option value={2}>some products</option>
            </Select>
          </FormControl>
      </Grid>

      <Grid item xs={6} className="vendor-container">
          <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel htmlFor="outlined-age-native-simple">Vendors</InputLabel>
            <Select
              onChange={productVendorChange}
              native
              inputProps={{
                name: 'vendors',
                id: 'outlined-age-native-simple',
              }}
            >
              <option value="" />
              <option value={1}>some vendors</option>
              <option value={2}>some vendors</option>
            </Select>
          </FormControl>
      </Grid>

      <Grid item xs={12} className="title-container">
          <TextField
            value={productTitle} 
            onChange={handleTitleChange} 
            className="MuiInputBase-fullWidth"
            disabled={enableTitle}
            id="outlined-disabled"
            label="Product title"
            variant="outlined"
          />
      </Grid>


      <Grid item xs={3} className="strength-container">
          <FormControl variant="outlined" className={classes.formControl}>
          <InputLabel htmlFor="outlined-age-native-simple">Strength</InputLabel>
          <Select 
            native
            inputProps={{
              name: 'strength',
              id: 'outlined-age-native-simple',
            }}
          >
            <option value="" />
            <option value={30}>30mg</option>
            <option value={50}>50mg</option>
          </Select>
        </FormControl>
      </Grid>
      <Grid item xs={3} className="variant-container">
        <FormControl className={classes.margin} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-amount">Price</InputLabel>
          <OutlinedInput
            type="number"
            id="outlined-adornment-amount"
            onChange={handleChange('price')}
            startAdornment={<InputAdornment position="start">$</InputAdornment>}
            labelWidth={60}
          />
        </FormControl>
      </Grid>
      <Grid item xs={3} className="quantity-container">
        <FormControl className={classes.margin} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-amount">Quantity</InputLabel>
          <OutlinedInput
            type="number"
            id="outlined-adornment-amount"
            onChange={handleChange('quantity')}
            labelWidth={60}
          />
        </FormControl>
      </Grid>
      <Grid item xs={3} className="sku-container">
        <TextField id="outlined-basic" label="SKU" variant="outlined" />
      </Grid>
      

      {
      optionsArray.map((item, i) => 
         <Grid item xs={4} className="productType-container">
            <Variants 
              index={i} 
              disabled={ i == 0 ? false : (optionsArray[i-1] == '' ? true: false)} 
              onchange={optionsChange}>
            </Variants>
        </Grid>
      )
      }

      {generatedOptionsArray.map( (variant, i) => 
          <Grid item xs={12} key={i}> 
            {variant}
          </Grid>
      )}


      <div className="save-button">
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        startIcon={<SaveIcon />}
      >
        Save
      </Button>
      </div>
    </Grid>
  </form>
</div>
}