import React, { useCallback, useState } from 'react';
import { Button, FormLayout, ResourceList, Modal } from '@shopify/polaris';
import Option from "./Option"
import VariantItem from "./VariantItem"

export default function Variants({ productData, setProductData }) {
    const optionValues2D = (productData["options"] && productData["options"].map(option => option.values || [])) || []
    const productVariants = optionValues2D.length > 0 ? combos(optionValues2D).map(c => {
        return {
            ...(c[0] && { "option1": c[0] }),
            ...(c[1] && { "option2": c[1] }),
            ...(c[2] && { "option3": c[2] })
        }
    }) : [];

    return (<div>
        <div className="qwerty-tabscontent-section">
            <div><h2 className="Polaris-Heading">Options</h2></div>
            <Option productData={productData} setProductData={setProductData} index={0} />
            <Option productData={productData} setProductData={setProductData} index={1} />
            <Option productData={productData} setProductData={setProductData} index={2} />
        </div>
        {
            productVariants && productVariants.length > 0 &&
            <div className="qwerty-tabscontent-section top-lined qwerty-separated-F_Items">
                <div><h2 className="Polaris-Heading">Showing {productVariants.length} variants</h2></div>
                <div className="er-heading">
                    <span></span>
                    <span>Name</span>
                    <span>Price</span>
                    <span>Quantity</span>
                    {/* <span>Cost per item</span> */}
                    <span>SKU</span>
                    {/* <span>Barcode</span> */}
                </div>
                <div className="variants-wrapper">
                    {
                        //TODO make this look better
                        productVariants && productVariants.map((v, index) => <VariantItem key={index} variantData={v} options={productData["options"]}></VariantItem>)
                    }
                </div>
            </div>
        }
    </div>);
};

function combos(list, n = 0, result = [], current = []) {
    if (n === list.length) result.push(current)
    else list[n].forEach(item => combos(list, n + 1, result, [...current, item]))

    return result
}