import React, { useState, useEffect } from "react";
import { getItem } from "../services/products"
import {
    useParams
} from "react-router-dom";
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, Theme, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Details from "./Products/Details"
import SEO from "./Products/SEO"
import Grid, { GridSpacing } from '@material-ui/core/Grid';
import StorefrontIcon from '@material-ui/icons/Storefront';
import BugReportIcon from '@material-ui/icons/BugReport';
import ControlPointDuplicateIcon from '@material-ui/icons/ControlPointDuplicate';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import Variants from "./Products/Variants"

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    secondary_title: {
        fontSize: 14,
    },
    title: {
        fontSize: 24,
    },
}));

export default function ProductItem() {
    const [product, setProduct] = useState();
    const [error, setError] = useState();
    const [open, setOpen] = React.useState(false);
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    const handleOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };
    const handleChangeIndex = (index) => {
        setValue(index);
    };
    let { itemId } = useParams();

    useEffect(() => {
        getProduct(itemId);
    }, []);

    function getProduct(itemId) {
        return getItem(itemId).then(data => setProduct(data)).catch(err => setError(err.toString()));
    }

    return <div className={classes.root}>
        {product ? (<Grid container className={classes.grid} spacing={2}>
            <Grid item xs={12}>
                <Typography className={classes.title} color="textPrimary" gutterBottom>
                    {product.title}
                    <Grid item xs={12}>
                        <Button href={`https://${product.shop}/products/${product.handle}`} target="_blank">
                            <BugReportIcon /> See online
                    </Button>
                        <Button href={`https://${product.shop}/admin/products/${product.shopify_id}`} target="_blank">
                            <StorefrontIcon /> Shopify
                    </Button>
                    </Grid>
                </Typography>
            </Grid>
            <Grid item xs={12}>
                <AppBar position="static" color="default">
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                        variant="fullWidth"
                        aria-label="full width tabs example"
                    >
                        <Tab label="Details" {...a11yProps(0)} />
                        <Tab label="Variants" {...a11yProps(1)} />
                        <Tab label="SEO" {...a11yProps(2)} />
                    </Tabs>
                </AppBar>
                {/* <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={value}
                    onChangeIndex={handleChangeIndex}
                > */}
                <TabPanel value={value} index={0} dir={theme.direction}>
                    <Details product={product} value={value} />
                </TabPanel>
                <TabPanel value={value} index={1} dir={theme.direction}>
                    <Variants variants={product.variants} />
                </TabPanel>
                <TabPanel value={value} index={2} dir={theme.direction}>
                    <SEO />
                </TabPanel>
                {/* </SwipeableViews> */}
            </Grid>
        </Grid>) : (<div>loading...</div>)}
    </div>
}