import React, { useCallback, useState, useEffect } from 'react';
import { Icon, DisplayText, Button } from '@shopify/polaris';
import { useParams, useRouteMatch, useHistory } from "react-router-dom";
import {
    CircleTickMajor
} from '@shopify/polaris-icons';

export default function Tabs({ title, tabs, data }) {

    // const [selectedTab, setSelectedTab] = useState({ ...tabs[0] })
    const history = useHistory();

    const { section } = useParams();

    const match = useRouteMatch();

    const selectedTab = tabs.find(t => t["id"] === section) || tabs[0];
    const handleTabChange = useCallback(
        (value) => {
            goToSection(value, match, history)
        },
        [match, history],
    );
    const goToSection = function (sectionName, match, history) {
        const newURL = match.path.replace(':itemId', match.params["itemId"]).replace(":section?", sectionName)
        history.push(newURL);
    }
    const handleNextSection = useCallback(
        () => {
            const currentSectionIndex = section ? tabs.findIndex(t => t["id"] === section) : 0;
            const nextTabIndex = currentSectionIndex + 1
            if (nextTabIndex < tabs.length) {
                goToSection(tabs[nextTabIndex]["id"], match, history)
            }
        },
        [section, match, history],
    );
    return (
        <div className="qwerty-tabs">
            <div className="qwerty-tabstitle">
                <DisplayText element="h2" size="medium">{title}</DisplayText>
            </div>
            <div className="qwerty-tabs-wrapper">
                <ul role="tablist" className="qwerty-tabslist">
                    {
                        tabs.map(tab => {
                            const tabDataIsComplete = typeof tab.isCompleted === 'function' && tab.isCompleted(data)
                            return (<li role="presentation" className={`qwerty-tabsitem ${tabDataIsComplete ? 'qwerty-tabitem-complete' : ''} ${tab["id"] == selectedTab["id"] ? 'qwerty-tabitem-active' : ''}`} onClick={() => { handleTabChange(tab["id"]) }} key={`qwerty-ctr-${tab["id"]}`}>
                                <button className="qwerty-tabsitem-button"><span className="qwerty-tabitem-icon">{tabDataIsComplete ? <Icon source={CircleTickMajor} /> : tab["icon"] && <Icon source={tab["icon"]} />}</span><span className="qwerty-tabitem-title">{tab["title"]}</span></button>
                            </li>)
                        })
                    }
                </ul>
                <div className="qwerty-tabscontent">
                    {
                        <div role="presentation">{typeof selectedTab["content"] == 'function' ? selectedTab["content"](data) : selectedTab["content"]}</div>
                    }
                    <div className="Polaris-Card__Footer">
                        <Button primary onClick={handleNextSection}>Next</Button>
                    </div>
                </div>
            </div>
        </div>
    )
}
