import React, { useState, useEffect } from "react";
import Images from "./Images"
import {
    useParams
} from "react-router-dom";
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';
import Grid, { GridSpacing } from '@material-ui/core/Grid';
import StorefrontIcon from '@material-ui/icons/Storefront';
import BugReportIcon from '@material-ui/icons/BugReport';
import ControlPointDuplicateIcon from '@material-ui/icons/ControlPointDuplicate';
import Modal from '@material-ui/core/Modal';
import Mentions from "../../Mentions"
import Portal from '@material-ui/core/Portal';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import * as service from "../../services/productTypes"

const useStyles = makeStyles({
    grid: {
        flexGrow: 1,
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    secondary_title: {
        fontSize: 14,
    },
    title: {
        fontSize: 24,
    },
    pos: {
        marginBottom: 12,
    },
});

export default function Details({ product }) {
    const [error, setError] = useState();
    const [open, setOpen] = useState(false);
    const [productTypes, setProductTypes] = useState([]);
    const [productVendors, setProductVendors] = useState([]);
    const [productTypeId, setproductTypeId] = useState(product && product.productType && product.productType.id);
    const [productVendorId, setproductVendorId] = useState(product && product.productVendor && product.productVendor.id);

    const classes = useStyles();
    let { itemId } = useParams();
    const container = React.useRef(null);
    useEffect(() => {
        getAllProductTypes();
    }, []);
    const getAllProductTypes = () => {
        return service.getAll().then(data => setProductTypes(data)).catch(err => setError(err.toString()));
    }
    const handleOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };
    const handleProductTypeChange = (event) => {
        const value = event.target.value;
        setproductTypeId(value)
    }
    const handleProductVendorChange = (event) => {
        const value = event.target.value;
        setproductTypeId(value)
    }
    return (
        <div>
            <Grid container className={classes.grid} spacing={2}>
                <Grid item xs={7}>
                    <Card className={classes.root} variant="outlined">
                        <CardContent>
                            <Portal container={container.current}>
                                <Mentions />
                            </Portal>
                            <div ref={container} />
                        </CardContent>
                    </Card>
                </Grid>

                <Grid item xs={5}>
                    <FormControl variant="outlined" className={classes.formControl}>
                        <InputLabel htmlFor="productType">Product Type</InputLabel>
                        <Select
                            native
                            value={productTypeId}
                            onChange={handleProductTypeChange}
                            label="ProductType"
                            inputProps={{
                                name: 'productType',
                                id: 'productType',
                            }}
                        >
                            {
                                productTypes && productTypes.map(t => <option key={t.id} value={t.id}>{t.name}</option>)
                            }
                        </Select>
                    </FormControl>
                    <Card className={classes.root} variant="outlined">
                        <CardContent>
                            <Typography className={classes.secondary_title} color="textSecondary" gutterBottom>
                                IMAGES
                            </Typography>
                            <Images images={product.images} />
                        </CardContent>
                        {/* <CardActions>
                    <Button size="small">Learn More</Button>
                    </CardActions> */}
                    </Card>
                </Grid>
            </Grid>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
                <div>Hi there</div>
            </Modal>
        </div >
    )
}