import React, { useState, useCallback } from 'react';
import { Card, TextContainer, Layout, Collapsible, FormLayout, Checkbox, TextField } from '@shopify/polaris';
const variantStyle = {
    minWidth: '230px',
    display: 'inline-block',
    margin: '5px'
}
const imgStyles = {
    maxHeight: '60px',
    display: 'inline-block'
}
export default function Variants() {
    const [active, setActive] = useState(false);
    const handleToggle = useCallback(() => setActive((active) => !active), []);

    return (
        <Layout>
            <Layout.Section>
                <Card>
                    <Card.Section actions={[{ content: 'Edit', onAction: handleToggle }]}>
                        <img style={imgStyles} src="https://cdn.shopify.com/s/files/1/0287/1354/9955/products/13th-Floor-APPLE-PUCKER-Nicotine-Salt-30ml.jpg?v=1593971256" />
                        <span style={variantStyle}>
                            <div>
                                <span style={variantStyle}><strong>Strength</strong>: 40 mg Salt Based Nicotine</span>
                                <span style={variantStyle}><strong>Size</strong>: 20</span>
                            </div>
                            <div>
                                <span style={variantStyle}><strong>SKU (Stock Keeping Unit)</strong>: Django40</span>
                                <span style={variantStyle}><strong>Barcode (ISBN, UPC, GTIN, etc.)</strong>: 852870008044</span>
                            </div>
                        </span>
                        <Collapsible
                            open={active}
                            id="basic-collapsible"
                            transition={{ duration: '150ms', timingFunction: 'ease' }}
                        >
                            <FormLayout>
                                <FormLayout.Group>
                                    <TextField type="text" label="Strength" value="40 mg Salt Based Nicotine" />
                                    <TextField type="text" label="Size" value="20" />
                                </FormLayout.Group>
                            </FormLayout>

                        </Collapsible>

                    </Card.Section>
                    <Card.Section actions={[{ content: 'Edit' }]}>
                        <img style={imgStyles} src="https://cdn.shopify.com/s/files/1/0287/1354/9955/products/13th-Floor-APPLE-PUCKER-Nicotine-Salt-30ml.jpg?v=1593971256" />
                        <span style={variantStyle}>
                            <div>
                                <span style={variantStyle}><strong>Strength</strong>: 30 mg Salt Based Nicotine</span>
                                <span style={variantStyle}><strong>Size</strong>: 10</span>
                            </div>
                            <div>
                                <span style={variantStyle}><strong>SKU (Stock Keeping Unit)</strong>: Django30</span>
                                <span style={variantStyle}><strong>Barcode (ISBN, UPC, GTIN, etc.)</strong>: 852870008033</span>
                            </div>
                        </span>

                    </Card.Section>
                </Card>

            </Layout.Section>
            <Layout.Section secondary>
                <Card sectioned title="Variants" actions={[{ content: 'Add variant' }]}>
                    <Card.Section>
                        <p>View a summary of your online stores performance.</p>
                    </Card.Section>

                    <Card.Section>
                        <p>
                            View a summary of your online stores performance, including sales,
                            visitors, top products, and referrals.
    </p>
                    </Card.Section>
                </Card>
            </Layout.Section>
        </Layout>



    );
}
