import React, { Component, useState, useEffect } from 'react';
import '../App.css';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import * as service from "../services/products"
import { useHistory, useRouteMatch } from "react-router-dom";
import ProductsToolbar from "./Products/ProductsToolbar"
import { Card, DataTable, Page, Icon, Button, ButtonGroup, Link } from '@shopify/polaris';
import { toArrayOfProps, Column } from "../utils/dataTableHelper"
import { PlusMinor } from '@shopify/polaris-icons';
import { syncAllProducts } from '../services/products'

const Listing: React.FC = () => {
  const [products, setProducts] = useState([] as any);
  const [error, setError] = useState();
  const [page, setPage] = useState(1);
  const history = useHistory();
  let grid: any = null;
  let match = useRouteMatch();

  useEffect(() => {
    getAllProducts();
  }, [0]);


  const [syncInProgress, setSyncInProgress] = useState(false);

  const columns: Column[] = [
    { name: "title", renderer: (product) => <Link removeUnderline url={`/dashboard/Listings/edit/${product['_id']}`}>{product["title"]}</Link> },
    { name: "vendor" },
    { name: "product_type" },
    { name: "status" },
  ]

  const getAllProducts = () => {
    return service.getAll().then(data => {
      const rows = toArrayOfProps(data, columns)
      setProducts(rows)
    }).catch(err => setError(err.toString()));
  }

  const onSyncProducts = () => {
    setSyncInProgress(true)
    syncAllProducts().then(data => { setSyncInProgress(false) }).catch(e => { })
  }

  const actions = <ButtonGroup>
    <Button loading={syncInProgress} onClick={onSyncProducts}>Sync products</Button>
    <Button primary onClick={() => {
      history.push(`/dashboard/Listings/create`)
    }}>Add product</Button>
  </ButtonGroup>

  return (
    <Page title="Listings"
      fullWidth
      primaryAction={actions}
      pagination={{
        hasNext: true,
      }}
    >
      <Card>
        <DataTable
          columnContentTypes={[
            'text',
            'text',
            'text',
            'text'
          ]}
          headings={[
            'Product',
            'Vendor',
            'Type',
            'Status'
          ]}
          rows={products}
          footerContent={`Showing ${products.length} of ${products.length} results`}
        />
      </Card>
    </Page>
  );
}

export default Listing;
