import React from 'react';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import { AnyAaaaRecord } from 'dns';
import TextField from '@material-ui/core/TextField';
import SnackbarContent from '@material-ui/core/SnackbarContent';
import Popover from '@material-ui/core/Popover';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import Fab from '@material-ui/core/Fab';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { Banner } from '@shopify/polaris';

const useStyles = makeStyles((theme) =>
    ({
        root: {
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-around',
            overflow: 'hidden',
            backgroundColor: theme.palette.background.paper,
        },
        gridList: {
            width: 500,
            height: 450,
            borderColor: "#c3c3c3",
        },
        title: {
            color: theme.palette.primary.light,
        },
        tilebar: {
            color: 'rgba(255, 255, 255, 0.54)',
        },
        titleBar: {
            background:
                'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
        },
    })
)

export default function SingleLineGridList(data) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const { images } = data;
    const classes = useStyles();
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;
    const action = (
        <>
            <Fab aria-describedby={id} color="info" onClick={handleClick} size="small">
                <HelpOutlineIcon />
            </Fab>
            <Popover
                id={id}
                open={open}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
            >
                <Typography className={classes.typography}>This is important for your SEO. To see what it affects go to <a href="#">details</a></Typography>
            </Popover>
        </>
    );
    return (
        <div className={classes.root}>
            <GridList cellHeight={260} className={classes.gridList} cols={4}>
                {images.map((tile) => (
                    <GridListTile key={tile.src} cols={2}>
                        <img src={tile.src} alt={tile.title} className={classes.image} />
                        asd
                        <GridListTileBar
                            title={tile.title}
                            subtitle={<TextField
                                className={classes.tilebar}
                                name="altext"
                                size="small"
                                fullWidth
                                label="Alt"
                                value={tile.alt}
                                autoFocus
                            />}
                        />

                    </GridListTile>
                ))}
            </GridList>
        </div>
    );
}