import React, { useRef } from 'react';
import { Stack, Banner, Caption, DropZone, List, Thumbnail } from '@shopify/polaris';


export default function MediaItem({ id, file, index, moveCard, product }) {

    return (<div className="vjd-thumbnail">
            <Thumbnail
                size="large"
                alt={file.name}
                source={window.URL.createObjectURL(file)}
            />
            <div>
                Alt: {product.title}
            </div>
    </div>);
};
