import React, { useCallback, useState, useEffect, useRef } from "react";
import { Layout, TextField, Banner, Link, Button, ContextualSaveBar, FormLayout, Checkbox } from "@shopify/polaris";
import Dropdown from '../../Helpers/Dropdown'
import { getAll as getAllProductTypes } from "../../../services/productTypes"
import { getAll as getAllVendors } from "../../../services/productVendors"
import Mentions from "../../Helpers/Mentions"
import * as productService from "../../../services/products"

export default function TextFieldExample({ productData, setProductData }) {

    useEffect(() => {
        const status = productData['status'] ?? 'active'
        setProductData({ ...productData, status })
    }, []);

    const handleProductTypeSelect = useCallback(
        (value) => {
            const newData = { ...productData, "product_type": value.name }
            if (productData["vendor"] && !newData["title"])
                newData["title"] = `${productData["vendor"]} ${value.name}`
            setProductData(newData)
        },
        [productData],
    );

    const handleVendorSelect = useCallback(
        (value) => {
            const newData = { ...productData, "vendor": value.name }
            if (productData["product_type"] && !newData["title"])
                newData["title"] = `${value.name} ${productData["product_type"]}`
            setProductData(newData)
        },
        [productData],
    );
    const handleDescriptionChange = useCallback(
        (value) => {
            setProductData({ ...productData, "body_html": value })
        },
        [productData],
    );
    const [checked, setChecked] = useState(false);
    const handleChange = useCallback((newChecked) => setChecked(newChecked), []);

    const handleStatusChange = useCallback(
        (active) => {
            debugger
            const status = active ? 'active' : 'draft'
            setProductData({ ...productData, status })
        },
        [productData],
    );
    return <div className="qwerty-tabscontent-section">
        <FormLayout>
            <div className="form-items-right">
                <Checkbox
                    label="Active"
                    checked={productData['status'] === 'active'}
                    onChange={handleStatusChange}
                />
            </div>
            <FormLayout.Group condensed>
                <Dropdown label="Vendor" value={productData["vendor"]} placeholder="e.g. Nike" getData={getAllVendors} labelFieldName="name" onSelect={handleVendorSelect} />
                <Dropdown label="Product type" value={productData["product_type"]} placeholder="e.g. Shirts" getData={getAllProductTypes} labelFieldName="name" onSelect={handleProductTypeSelect} />
            </FormLayout.Group>
            <FormLayout.Group>
                <TextField
                    label="Title"
                    type="text"
                    value={productData["title"]}
                    helpText="The title should be around 10-70 characters"
                    onChange={(value) => { setProductData({ ...productData, "title": value }) }}
                />
            </FormLayout.Group>
            <FormLayout.Group>
                <Mentions onChange={handleDescriptionChange} label="Description" htmlString={productData["body_html"]} />
            </FormLayout.Group>
        </FormLayout>
    </div>
}
