import React, { useCallback, useState } from 'react';
import { Stack, Banner, Caption, DropZone, List, Thumbnail } from '@shopify/polaris';
import MediaItem from "./MediaItem"

export default function DropZoneWithImageFileUpload({productData}) {
  const [files, setFiles] = useState([]);
  const [rejectedFiles, setRejectedFiles] = useState([]);
  const hasError = rejectedFiles.length > 0;

  const handleDrop = useCallback(
    (_droppedFiles, acceptedFiles, rejectedFiles) => {
      setFiles((files) => [...files, ...acceptedFiles]);
      setRejectedFiles(rejectedFiles);
    },
    [],
  );

  const fileUpload = !files.length && <DropZone.FileUpload />;


  const errorMessage = hasError && (
    <Banner
      title="The following images couldnt be uploaded:"
      status="critical"
    >
      <List type="bullet">
        {rejectedFiles.map((file, index) => (
          <List.Item key={index}>
            {`"${file.name}" is not supported. File type must be .gif, .jpg, .png or .svg.`}
          </List.Item>
        ))}
      </List>
    </Banner>
  );

  const uploadedFiles = files.length > 0 && (
    <Stack vertical={false}>
      {files.map((card, i) => renderCard(card, i, productData))}
    </Stack>
  );

  return (
      <Stack vertical>
        {errorMessage}
        {uploadedFiles}
        <div style={{ width: 114, height: 114 }}>
          <DropZone accept="image/*" type="image" onDrop={handleDrop}>
            <DropZone.FileUpload />
          </DropZone>
        </div>
      </Stack>
  );
}

const renderCard = (file, index, productData) => {
  return (<MediaItem  file={file} product={productData} />);
};