import React, { useCallback, useState } from "react";
import { TextField, Card, Banner, Link } from "@shopify/polaris";

export default function TextFieldExample() {
  const [value, setValue] = useState("J Pixel");

  const handleChange = useCallback((newValue) => setValue(newValue), []);

  return (
    <Card title="SEO" sectioned>
      <div style={{marginBottom:"20px"}}>
        <Banner onDismiss={() => { }} status="warning">
          <p>
            Your title should include 10-70 characters{' '}
            <Link url="">See more detailed explanation</Link>
          </p>
        </Banner>
      </div>

      <TextField label="Title" value={value} onChange={handleChange} />
      <TextField
        label="Store name"
        multiline={5}
        value={value}
        onChange={handleChange}
      />
    </Card>
  );
}
