import React from "react"
import { BrowserRouter as Router, Route, Redirect, useParams, Switch } from "react-router-dom"
import Templates from "../Templates"
import TemplateDetails from "../TemplateDetails"
import Listings from "../Listings"
import Vendors from "../Vendors"
import Users from "../Users"
import ViewUser from "../ViewUser"
import Settings from "../Settings"
import ProductItem from "../ProductItem"
import Edit from "../Edit"
import ProductTypes from "../ProductTypes"
import Options from "../Options"
import productVariants from "../productVariants"
import EditProductTypeItem from "../ProductTypes/Edit"
import CreateProductType from "../ProductTypes/Create"
import CreateProduct from "../Products/Create"
import CreateProductOld from "../Products/CreateWithStep"
import Variants from "../Products/Forms/Variants"

import Stats from "./Stats"
import { useRouteMatch } from 'react-router-dom'

export default function Routes() {
  const match = useRouteMatch();

  return <Switch>
    <Route exact path={`${match.path}/`} component={Stats} />
    <Route exact path={`${match.path}/Templates`} component={Templates} />
    <Route exact path={`${match.path}/Listings`} component={Variants} />
    <Route exact path={`${match.path}/Listings/create/:section?`} component={CreateProduct} />
    <Route exact path={`${match.path}/Listings/createold`} component={CreateProductOld} />
    <Route path={`${match.path}/Listings/edit/:itemId/:section?`} component={CreateProduct} />

    <Route path={`${match.path}/Listings/:itemId`} component={ProductItem} />
    <Route exact path={`${match.path}/Users`} component={Users} />
    <Route path={`${match.path}/Users/:usersID`} component={ViewUser} />
    <Route path={`${match.path}/Vendors`} component={ProductTypes} />
    <Route path={`${match.path}/Settings`} component={Settings} />
    <Route path={`${match.path}/Templates/:TemplateId`} component={TemplateDetails} />
    <Route path={`${match.path}/Templates/New`} component={Settings} />
    <Route exact path={`${match.path}/Edit/:EditID`} component={Edit} />
    <Route path={`${match.path}/Options`} component={Options} />
    <Route exact path={`${match.path}/ProductTypes`} component={ProductTypes} />
    <Route exact path={`${match.path}/ProductTypes/create`} component={CreateProductType} />
    <Route path={`${match.path}/ProductTypes/:itemId`} component={EditProductTypeItem} />
  </Switch>
}