import React, { useCallback, useState } from 'react';
import { FormLayout, TextField, Tag, Button } from '@shopify/polaris';
import { getAll as getAllOptions } from "../../../services/options"
import Dropdown from '../../Helpers/Dropdown'
import {
    MobilePlusMajor
} from '@shopify/polaris-icons';

export default function Variants({ productData, setProductData, index = 0 }) {
    const [optionValueText, setOptionValueText] = useState("");
    const initialOptionValues = (productData.options && productData.options[index] && productData.options[index]["values"]) || []

    const handleOptionSelect = useCallback(
        (option) => {
            const newData = { ...productData }
            newData["options"] = productData["options"] || []
            newData["options"][index] = newData["options"][index] || {}
            newData["options"][index]["name"] = option.name
            newData["options"][index]["values"] = option.values
            setProductData(newData)
        },
        [productData],
    );

    const handleOptionValueChanged = useCallback(
        (value) => {
            const optionValues = productData.options[index]["values"] || []
            if (value && !optionValues.some(v => v == value)) {
                const newData = { ...productData }
                newData.options[index]["values"] = [...optionValues, value]
                setProductData(newData)
            }
            setOptionValueText("")
        },
        [productData, optionValueText],
    );

    const handleOptionValueRemoved = useCallback(
        (value) => {
            const optionValues = productData.options[index]["values"] || []
            const newData = { ...productData }
            newData.options[index]["values"] = optionValues.filter(v => v !== value)
            setProductData(newData)
        },
        [productData, optionValueText],
    );
    const selectedOption = productData.options && productData.options[index] && productData.options[index].name
    return (<div className="options-grid">
        <FormLayout.Group condensed>
            <Dropdown showConnectedRight={false} label={`Option ${index + 1}`} value={selectedOption} getData={getAllOptions} labelFieldName="name" onSelect={handleOptionSelect} />
            <TextField
                label="Add value"
                type="text"

                value={optionValueText}
                autoComplete={false}
                onChange={setOptionValueText}
                connectedRight={
                    <Button
                        primary
                        disabled={!selectedOption} 
                        onClick={() => { handleOptionValueChanged(optionValueText) }} icon={MobilePlusMajor}></Button>
                }
            />
            {/* <Dropdown showConnectedRight={false} label="Add value" value={optionValueText} placeholder="Add option value" labelFieldName="name" onSelect={handleOptionValueChanged} /> */}
            <div>
                {
                    productData.options && productData.options[index] && productData.options[index]["values"] && productData.options[index]["values"].length > 0 && <div className="Polaris-Labelled__LabelWrapper"><div className="Polaris-Label"><label className="Polaris-Label__Text">Values</label></div></div>
                }
                {
                    productData.options && productData.options[index] && productData.options[index]["values"] && productData.options[index]["values"].map(value => <Tag key={value} onRemove={() => { handleOptionValueRemoved(value) }}>{value}</Tag>)
                }
            </div>
        </FormLayout.Group>
    </div>);
};