import React, { Component, useState, useEffect } from 'react';
import {
  GridComponent,
  ColumnDirective,
  ColumnsDirective,
  Page,
  Inject,
  Group,
  Sort,
  Edit,
  EditSettingsModel,
  Toolbar,
  ToolbarItems,
  CommandColumn
} from '@syncfusion/ej2-react-grids';
import { Button } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory, useRouteMatch } from "react-router-dom";
import {Banner} from '@shopify/polaris';

const useStyles = makeStyles({
  productTitle: {
    alignItems: 'center'
  }
});

const Listing: React.FC = (data: any) => {
  const variants: any = data.variants;
  const [error, setError] = useState();
  const history = useHistory();
  let grid: any = null;
  let match = useRouteMatch();

  const classes = useStyles();
  const editOptions: EditSettingsModel = { allowEditing: true, allowAdding: true, allowDeleting: true };
  const toolbarOptions: ToolbarItems[] = ['Add', 'Edit', 'Delete'];

  function productImageTemplate({ images, title }: any) {
    const image_src = images && images.length > 0 && images.sort((img: any) => img.position)[0]["src"]
    return (
      <Grid container className={classes.productTitle}>
        <Grid item xs={1}>
          <div className={`${image_src ? "image" : "no-image"}`}>
            {image_src ?
              <img src={image_src} /> :
              <svg viewBox="0 0 20 20" className="_1nZTW xvNMs"><path fill="currentColor" d="M14 9l-5 5-3-2-5 3v4h18v-6z"></path><path d="M19 0H1a1 1 0 0 0-1 1v18a1 1 0 0 0 1 1h18a1 1 0 0 0 1-1V1a1 1 0 0 0-1-1zM8 6c.55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1 .45-1 1-1zm0 4c1.654 0 3-1.346 3-3S9.654 4 8 4 5 5.346 5 7s1.346 3 3 3zm-6 8v-2.434l3.972-2.383 2.473 1.65c.398.264.925.21 1.262-.126l4.367-4.367L18 13.48V18H2zM18 2v8.92l-3.375-2.7a.999.999 0 0 0-1.332.073l-4.42 4.42-2.318-1.545a1 1 0 0 0-1.07-.025L2 13.233V2h16z"></path></svg>}
          </div>
        </Grid>
        <Grid item xs={5}>
          <span>{title}</span>
        </Grid>
      </Grid>
    );
  }

  function productTypeTemplate({ productType: { name } }: any) {
    return (
      <Grid container className={classes.productTitle}>
        <Grid item xs={5}>
          <span>{name}</span>
        </Grid>
      </Grid>
    );
  }

  function productVendorTemplate({ productVendor: { name } }: any) {

    return (
      <Grid container className={classes.productTitle}>
        <Grid item xs={5}>
          <span>{name}</span>
        </Grid>
      </Grid>
    );
  }
  function productStatusTemplate({ productVendor: { name } }: any) {

    return (
      <Grid container className={classes.productTitle}>
        <Grid item xs={5}>
          <span>{name}</span>
        </Grid>
      </Grid>
    );
  }

  function manageProduct(props: any) {
    return (<div className='btn_container'>
      <Button><a href={"/Edit/" + props.ID}>Edit</a></Button>
      <Button><a href={"/Listings/" + props.ID}>View</a></Button>
    </div>);
  }

  function rowSelected() {
    const selectedRow: any = grid && grid.getSelectedRecords();
    const product: any = selectedRow && selectedRow[0];
    const id: any = product && product._id;
    history.push(`${match.path}/${id}`)
  }

  const pageSettings = {
    pageSize: 6
  }

  return (
    <Card variant="outlined">

      <CardContent>
        <GridComponent ref={g => grid = g} rowSelected={rowSelected} dataSource={variants} enableHover={true} rowHeight={38} allowPaging={true} pageSettings={pageSettings} allowFiltering={true} allowSorting={true} allowSelection={true}>
          <ColumnsDirective>
            {/* <ColumnDirective field='Variant' headerText='Variant' template={productImageTemplate} width='370' /> */}
            <ColumnDirective field='title' headerText='Title' width='180' />
            <ColumnDirective field='price' headerText='Price' width='180' />
            <ColumnDirective field='sku' headerText='SKU' editType='dropdownedit' width='130' />
            {/* <ColumnDirective field='status' headerText='Type' template={productTypeTemplate} width='180' /> */}
          </ColumnsDirective>
          <Inject services={[Page, Group, Sort, Edit, Toolbar, CommandColumn]} />
        </GridComponent>
      </CardContent>
    </Card>
  );
}

export default Listing;