import React, { useCallback, useState, useEffect, useRef } from "react";
import { Layout, TextField, Card, Banner, Link, Button, ContextualSaveBar } from "@shopify/polaris";
import { DisplayText, Subheading, TextStyle, Heading, Stack, FormLayout, Page, Badge } from '@shopify/polaris';
import Dropdown from '../Helpers/Dropdown'
import { getAll as getAllProductTypes } from "../../services/productTypes"
import { getAll as getAllVendors } from "../../services/productVendors"
import { makeStyles } from '@material-ui/core/styles';
import StepItem from "../Helpers/StepItem"
import Tabs from "../Helpers/Tabs"
import Mentions from "../../Mentions"
import Deatils from "./Forms/Details"
import Media from "./Forms/Media"
import Variants from "./Forms/Variants"
import Organize from "./Forms/Organize"
import * as productService from "../../services/products"
import { useParams, useHistory } from "react-router-dom";
import { deepEqual } from "../../utils/operators"
import {
    AddProductMajor,
    ImagesMajor,
    VariantMajor,
    SearchMajor,
    CategoriesMajor
} from '@shopify/polaris-icons';

const useStyles = makeStyles({
    productTitle: {
        alignItems: 'center'
    }
});

export default function CreateEditProduct() {

    const [productData, setProductData] = useState({})
    const [product_db_data, setProduct_db_data] = useState({})

    const animationWrapperRef = useRef(null);
    const animation1 = useRef(null);
    const animation2 = useRef(null);
    const [isDirty, setIsDirty] = useState(true);
    const history = useHistory();

    const [animationWrapperHeight, setAnimationWrapperHeight] = useState();
    const classes = useStyles();

    let { itemId } = useParams();

    useEffect(() => {
        itemId && productService.getItem(itemId).then(data => {
            setProduct_db_data(data)
            setProductData(data)
        }).catch(e => console.log(e))
    }, [itemId]);


    useEffect(() => {
        setIsDirty(!deepEqual(productData, product_db_data))
    }, [productData]);

    const toggleIsDirty = useCallback(
        () => setIsDirty((isDirty) => !isDirty),
        [],
    );

    const saveChanges = () => {
        //TODO every step should save only its data
        itemId ? productService.update(productData, itemId).then(data => {
            setProduct_db_data(data)
            setProductData(data)
        })
            :
            productService.create(productData).then(data => {
                history.push(`/dashboard/Listings/edit/${data.product._id}`)
            });
    }
    const contextualSaveBarMarkup = isDirty ? (
        <ContextualSaveBar
            message="Unsaved product"
            saveAction={{
                onAction: saveChanges,
            }}
            discardAction={{
                onAction: toggleIsDirty,
            }}
        />
    ) : null;
    const tab_title = <span>
        {productData["title"]} {productData["_id"] && <Badge status={`${productData["status"] == 'active' ? 'success' : 'info'}`}>{productData["status"]} </Badge>}
    </span>
    return <Page
        breadcrumbs={[{ content: 'Dashboard', url: '/Dashboard/' }]}
        title={itemId ? "Edit Product" : "New Product"}
    >
        <Layout>
            {contextualSaveBarMarkup}
            {/* <Layout.Section>
            <Banner
                status="warning"
                secondary={{ content: 'Learn more' }}
                onDismiss={() => { }}
            >
                <p>
                    Add weights to show accurate rates at checkout and when buying shipping
                    labels in Shopify.
                </p>
            </Banner>
        </Layout.Section> */}

            <Layout.Section>
                {productData && <Tabs title={tab_title} data={productData} tabs={[
                    { id: "details", icon: AddProductMajor, isCompleted: DetailsCompleted, title: "Details", content: (data) => <Deatils productData={data} setProductData={setProductData} /> },
                    { id: "organize", icon: CategoriesMajor, title: "Organize", content: <Organize productData={productData} setProductData={setProductData}/> },
                    { id: "variants", icon: VariantMajor, title: "Variants", content: (data) => <Variants productData={data} setProductData={setProductData} /> },
                    { id: "images", icon: ImagesMajor, title: "Media", content: <Media productData={productData} /> },
                    { id: "seo", icon: SearchMajor, title: "SEO", content: <div>{productData["title"]}</div> }
                ]} />}
            </Layout.Section>
            <Layout.Section>

                {/* <StepItem activator={<div>Write some awesome description</div>}>
                <Mentions />
            </StepItem>
            <StepItem activator={<div>Organize it</div>}></StepItem>
            <StepItem activator={<div>Add some really cool media for your product</div>}></StepItem>
            <StepItem activator={<div>Does it have variations?</div>}></StepItem>
            <StepItem activator={<div>Write some great text for search engines</div>}></StepItem> */}

            </Layout.Section>
        </Layout>

    </Page>


}

function DetailsCompleted(productData) {
    return productData["title"] && productData["vendor"] && productData["product_type"] && productData["body_html"]
}
