import React, { useCallback, useState } from 'react';
import { Button, FormLayout, TextField, ResourceItem, Card, Checkbox, Thumbnail } from '@shopify/polaris';



export default function VariantItem({ variantData, options, setData }) {

    const title = (variantData["option1"] || '') + (variantData["option2"] ? `\\${variantData["option2"]}` : '') + (variantData["option3"] ? `\\${variantData["option3"]}` : '')

    const handleChange = useCallback((value, field) => {
        // setData({ ...variantData, [field]: value })
    }, [variantData])



    return (<div className="variant-item">
        <FormLayout.Group condensed>
            <Thumbnail
                source="https://burst.shopifycdn.com/photos/black-leather-choker-necklace_373x@2x.jpg"
                size="medium"
            />
            <div>{title}</div>
            <TextField
                // label="Price"
                type="string"
                value={variantData["price"]}
                prefix="$"
                onChange={(value) => { handleChange(value,"price") }}
            />
            <TextField
                // label="Price"
                type="number"
                value={variantData["quantity"]}
                onChange={(value) => { handleChange(value,"quantity") }}
            />
            <TextField
                // label="SKU"
                type="text"
                value={variantData["compare_at_price"]}
                onChange={(value) => { handleChange(value,"compare_at_price") }}
            />
        </FormLayout.Group>
    </div>);
};
