import React, { useReducer, useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import RichTextEditor from "../Slate/RichTextEditor"
import escapeHtml from 'escape-html'
import { Node, Text } from 'slate'
import { jsx } from 'slate-hyperscript'


const useStyles = makeStyles((theme) => ({
  root: {
    padding: '10px'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  richEditor: {
    padding: "25px",
    border: "solid 1px rgba(0, 0, 0, 0.23)",
    borderRadius: "4px",
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

const initialValue =
  [
    {
      type: 'paragraph',
      children: [
        { text: 'This is editable ' },
        { text: 'rich', bold: true },
        { text: ' text, ' },
        { text: 'much', italic: true },
        { text: ' better than a ' },
        { text: '<textarea>', code: true },
        { text: '!' },
      ],
    },
    {
      type: 'paragraph',
      children: [
        {
          text:
            "Since it's rich text, you can do things like turn a selection of text ",
        },
        { text: 'bold', bold: true },
        {
          text:
            ', or add a semantically rendered block quote in the middle of the page, like this:',
        },
      ],
    },
    {
      type: 'block-quote',
      children: [{ text: 'A wise quote.' }],
    },
    {
      type: 'paragraph',
      children: [{ text: 'Try it out for yourself!' }],
    },
  ]

export default function Form({ initialData, onSubmit }) {
  const classes = useStyles();
  const [error, setError] = useState();

  const [userInput, setUserInput] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    {
      ...initialData
    }
  );

  function handleSubmit(event) {
    event.preventDefault();
    const data = { ...userInput }
    onSubmit(data)
  }

  const handleChange = evt => {
    const name = evt.target.name;
    const newValue = evt.target.value;
    setUserInput({ [name]: newValue });
  }

  const serialize = node => {
    if (Text.isText(node)) {
      return escapeHtml(node.text)
    }

    const children = node.children && node.children.map(n => serialize(n)).join('')

    switch (node.type) {
      case 'block-quote':
        return `<blockquote>${children}</blockquote>`
      case 'paragraph':
        return `<p>${children}</p>`
      case 'link':
        return `<a href="${escapeHtml(node.url)}">${children}</a>`
      case 'bulleted-list':
        return `<ul>${children}</ul>`
      case 'heading-one':
        return `<h1>${children}</h1>`
      case 'heading-two':
        return `<h2>${children}</h2>`
      case 'list-item':
        return `<li>${children}</li>`
      case 'numbered-list':
        return `<ol>${children}</ol>`
      default:
        return children
    }
  }

  const deserialize = el => {
    if (el.nodeType === 3) {
      return el.textContent
    } else if (el.nodeType !== 1) {
      return null
    }

    const children = Array.from(el.childNodes).map(deserialize)

    switch (el.nodeName) {
      case 'BODY':
        return jsx('fragment', {}, children)
      case 'BR':
        return '\n'
      case 'BLOCKQUOTE':
        return jsx('element', { type: 'block-quote' }, children)
      case 'P':
        return jsx('element', { type: 'paragraph' }, children)
      case 'UL':
        return jsx('element', { type: 'bulleted-list' }, children)
      case 'H1':
        return jsx('element', { type: 'heading-one' }, children)
      case 'H2':
        return jsx('element', { type: 'heading-two' }, children)
      case 'LI':
        return jsx('element', { type: 'list-item' }, children)
      case 'OL':
        return jsx('element', { type: 'numbered-list' }, children)
      case 'A':
        return jsx(
          'element',
          { type: 'link', url: el.getAttribute('href') },
          children
        )
      default:
        return el.textContent
    }
  }

  const templateData = (userInput && userInput.template && deserialize(new DOMParser().parseFromString(userInput.template, 'text/html').body)) || initialValue 

  const handleRichTextChange = (nodes) => {
    const text = nodes.map(node => serialize(node)).join("");
    setUserInput({ "template": text });
  }
  return (
    <Container >
      <CssBaseline />
      <Paper className={classes.root}>
        {/* <Typography component="h1" variant="h5">
          Sign up
        </Typography> */}
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                name="name"
                variant="outlined"
                required
                fullWidth
                value={userInput.name}
                onChange={handleChange}
                label="Name"
                autoFocus
              />
            </Grid>
            <Grid item xs={12}>
              <div className={classes.richEditor}>
                <RichTextEditor initialValue={templateData} onChange={handleRichTextChange} />
              </div>
            </Grid>
            </Grid>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Save
          </Button>
        </form>
      </Paper>
    </Container>
  );
}