import React, { useState, useEffect } from "react";
import Form from './ProductTypeForm';
import { useParams,useHistory } from "react-router-dom";
import { update, getItem } from "../../services/productTypes"

export default function Create() {
    const [error, setError] = useState();
    const [productType, setProductType] = useState();
    let { itemId } = useParams();
    let history = useHistory();


    function getProductType(itemId) {
        return getItem(itemId).then(data => setProductType(data)).catch(err => setError(err.toString()));
    }

    useEffect(() => {
        getProductType(itemId);
    }, []);

    function handleSubmit(data) {
        update(data,itemId).catch(err => setError(err.toString()));
        history.goBack()
    }
    return (
        <div>
            {productType && <Form onSubmit={handleSubmit} initialData={productType} />}
        </div>
    )
}
